// scss-docs-start color-variables
$_black:     #000000 !default;
$_bg:        #F3F3F3 !default;
$_blue:      #00D1E0 !default;
$_dark-blue: #008993 !default;
$_dark-red:  #c70101 !default;
$_green:     #69BE67 !default;
$_input:     #b3bcc3 !default;
$_link:      #a0a0a0 !default;
$_orange:    #ffb934 !default;
$_red:       #ff0000 !default;
$_white:     #ffffff !default;

// scss-docs-start colors-map
$colors: (
  "bg":          $_bg,
  "black":       $_black,
  "dark-blue":   $_dark-blue,
  "dark-red":    $_dark-red,
  "delete":      $_red,
  "hover":       $_dark-blue,
  "orange":      $_orange, 
  "primary":     $_blue,
  "success":     $_green,
  "white":       $_white,
) !default;


//color bg, txt, btn landing page
@each $key, $val in $colors {
  .text-#{$key} {
    color: $val;
  }

  .bg-#{$key} {
    background: $val;
  }

  .btn-#{$key} {
    border: 1px solid $val;
  }
}

// scss-docs-start theme-color-variables
$_dark:         $_dark-blue;
$_dark-red:     $_dark-red; 
$_delete:       $_red;
$_hover:        $_dark-blue;
$_primary:      $_blue;
$_success:      $_green;


// text-color map black variant

$black:    #000000;
$black-90: #191919;
$black-80: #333333;
$black-70: #4D4D4D;
$black-60: #666666;
$black-50: #808080;
$black-40: #999999;
$black-30: #B3B3B3;
$black-20: #CCCCCC;
$black-10: #E6E6E6;

$textColor: (
  "black":  $black,
  "90":     $black-90,
  "80":     $black-80,
  "70":     $black-70,
  "60":     $black-60,
  "50":     $black-50,
  "40":     $black-40,
  "30":     $black-30,
  "20":     $black-20,
  "10":     $black-10,
) !default;

@each $key, $val in $textColor {
  .txt-black-#{$key} {
    color: $val;
  }

  .outline-#{$key} {
    border: 1px solid $val;
  }
}